<template>
	<gracePage :customHeader="false">
		<view slot="gBody" class="grace-body">
			<view class="grace-form grace-margin-top">
				<view class="grace-items">
					<text class="grace-icons icon-write icon grace-blue"></text>
					<view class="grace-label">姓名</view>
					<input type="text" class="input text-align-left" name="realName" v-model="realName" placeholder="请输入姓名" />
					<view class="grace-form-funs grace-icons icon-close" @tap="clearRealName" v-if="realName.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-address icon grace-blue"></text>
					<view class="grace-label">员工号</view>
					<input type="text" class="input text-align-left" name="code" v-model="code" placeholder="请输入员工号" />
					<view class="grace-form-funs grace-icons icon-close" @tap="clearCode" v-if="code.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-face icon grace-blue"></text>
					<view class="grace-label">性别</view>
					<view>
						<radio-group @change="genderChange">
							<label class="grace-check-item margin-right-ten">
								<radio value="男" :checked="genderValue === '男'"></radio>
								男
							</label>
							<label class="grace-check-item">
								<radio value="女" :checked="genderValue === '女'"></radio>
								女
							</label>
							<!-- 						<label v-for="(item, index) in genderTypes" :key="index" class="grace-check-item margin-right-ten">
							<radio :value="item.value" :checked="item.checked"></radio> {{item.name}}
						</label> -->
						</radio-group>
					</view>
					<view class="margin-right-100"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-time icon grace-blue"></text>
					<view class="grace-label">生日</view>
					<picker mode="date" v-model="birthday" :start="startDate" :end="endDate" name="birthday" @change="sbindDateChange">
						<view class="uni-input">{{ birthday }}</view>
					</picker>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-tel icon grace-blue"></text>
					<view class="grace-label">手机号码</view>
					<input type="text" class="input text-align-left" name="mobile" v-model="mobile" placeholder="请输入手机号码" />
					<view class="grace-form-funs grace-icons icon-close" @tap="clearMobile" v-if="mobile.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-tel icon grace-blue"></text>
					<view class="grace-label">电话号码</view>
					<input type="text" class="input text-align-left" name="telephone" v-model="telephone" placeholder="请输入电话号码" />
					<view class="grace-form-funs grace-icons icon-close" @tap="clearTelephone" v-if="telephone.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-write icon grace-blue"></text>
					<view class="grace-label">邮箱</view>
					<input type="text" class="input text-align-left" name="email" v-model="email" placeholder="请输入邮箱" />
					<view class="grace-form-funs grace-icons icon-close" @tap="clearEmail" v-if="email.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-qq icon grace-blue"></text>
					<view class="grace-label">QQ</view>
					<input type="text" class="input text-align-left" name="qq" v-model="qq" placeholder="请输入QQ" />
					<view class="grace-form-funs grace-icons icon-close" @tap="clearQq" v-if="qq.length > 0"></view>
				</view>

				<view class="grace-items">
					<text class="grace-icons icon-weixin icon grace-blue"></text>
					<view class="grace-label">微信</view>
					<input type="text" class="input text-align-left" name="wechat" v-model="wechat" placeholder="请输入微信" />
					<view class="grace-form-funs grace-icons icon-close" @tap="clearWechat" v-if="wechat.length > 0"></view>
				</view>

				<view class="grace-margin-top"><button type="primary" class="grace-button" @click="changeUserInfor">更改用户信息</button></view>
			</view>
		</view>
	</gracePage>
</template>

<script>
function getDate(type) {
	const date = new Date();

	let year = date.getFullYear();
	let month = date.getMonth() + 1;
	let day = date.getDate();

	if (type === 'start') {
		year = year - 5;
	} else if (type === 'end') {
		year = year + 1;
	}
	month = month > 9 ? month : '0' + month;
	day = day > 9 ? day : '0' + day;

	return '${year}-${month}-${day}';
}
export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {},
			userContact: {},
			realName: '',
			code: '',
			//genderTypes: [{name: '男',value: '1',checked: false},{name: '女',value: '0',checked: false}],
			genderValue: '',

			startDate: getDate('start'),
			endDate: getDate('end'),
			birthday: ' ',
			mobile: '',
			telephone: '',
			email: '',
			qq: '',
			wechat: ''
		};
	},
	onLoad: function() {
		let me = this;
		let serverUrl = me.getServerUrl();
		let userInfo = me.getCurrentUserInfo();
		if (userInfo != null) {
			me.userInfo = userInfo;
			// console.log(userInfo);
			uni.request({
				url: serverUrl + '/User/GetUserContact',
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					//console.log(res.data.Data);
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							me.userIsLogin = false;
							uni.showToast({
								title: '用户已异地登录',
								duration: 2000,
								icon: 'none'
							});
						} else {
							me.userContact = res.data.Data;
							me.realName = userInfo.RealName != null ? userInfo.RealName : '';
							me.code = res.data.Data.Code != null ? res.data.Data.Code : '';
							//me.genderValue = res.data.Data.Gender == '男' ? '1' : res.data.Data.Gender == '女' ? '0' : '';
							me.genderValue = res.data.Data.Gender;
							me.birthday = res.data.Data.Birthday;
							me.mobile = res.data.Data.Mobile != null ? res.data.Data.Mobile : '';
							me.telephone = res.data.Data.Telephone != null ? res.data.Data.Telephone : '';
							me.email = res.data.Data.Email != null ? res.data.Data.Email : '';
							me.qq = res.data.Data.Qq != null ? res.data.Data.Qq : '';
							me.wechat = res.data.Data.WeChat != null ? res.data.Data.WeChat : '';
						}
					} else {
						//debugger;
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
		} else {
			me.userInfo = {};
			uni.reLaunch({
				url: 'login'
			});
		}
	},
	methods: {
		clearRealName() {
			this.realName = '';
		},
		clearCode() {
			this.code = '';
		},
		clearMobile() {
			this.mobile = '';
		},
		clearTelephone() {
			this.telephone = '';
		},
		clearEmail() {
			this.email = '';
		},
		clearQq() {
			this.qq = '';
		},
		clearWechat() {
			this.wechat = '';
		},
		genderChange: function(e) {
			this.genderValue = e.target.value;
			//console.log(e.target.value);
		},
		sbindDateChange: function(e) {
			this.birthday = e.target.value;
		},
		changeUserInfor() {
			let me = this;
			let serverUrl = me.getServerUrl();
			let userInfo = me.getCurrentUserInfo();
			if (me.realName == '') {
				uni.showToast({
					title: '请输入姓名',
					duration: 2000,
					icon: 'none'
				});
				return;
			}

			//encodeURI(JSON.stringify(me.realName))
			//me.genderValue == '1' ? '男' : me.genderValue == '0' ? '女' : ''

			uni.request({
				url:
					serverUrl +
					'/User/ChangeUserInfo?realName=' +
					me.realName +
					'&code=' +
					me.code +
					'&gender=' +
					me.genderValue +
					'&birthday=' +
					me.birthday +
					'&mobile=' +
					me.mobile +
					'&telephone=' +
					me.telephone +
					'&email=' +
					me.email +
					'&qq=' +
					me.qq +
					'&weChat=' +
					me.wechat,
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					//console.log(res.data);
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							me.userIsLogin = false;
							uni.showToast({
								title: '用户已异地登录',
								duration: 2000,
								icon: 'none'
							});
						} else {
							uni.showToast({
								title: '更新成功',
								duration: 2000,
								icon: 'success'
							});
							var userInfo = res.data.Data;
							//向userInfo添加过期日期
							var cookieHours = me.getCookieHours();
							var timeNow = new Date();
							var expiredDate = timeNow.setTime(timeNow.setHours(timeNow.getHours() + cookieHours));
							userInfo.expiredDate = expiredDate;
							// 保存用户信息到全局的缓存中
							uni.setStorageSync('currentUserInfo', userInfo);
						}
					} else {
						//debugger;
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
		},
		onNavigationBarButtonTap(e) {
			if (e.type == 'back') {
				uni.reLaunch({
					url: 'usercenter'
				});
			}
		}
	}
};
</script>

<style>
.margin-right-ten {
	margin-right: 100upx;
}
.margin-right-100 {
	margin-right: 100upx;
	margin-left: 100upx;
}
</style>
